:root {
  --patient-list-border-color: #acacac;
  --patient-list-width: 1120px;
  --patient-list-firstWidth: 100px;
  --patient-list-secondWidth: 300px;
  --patient-list-thirdWidth: 100px;
  --patient-list-fourthWidth: 200px;
  --patient-list-fifthWidth: 270px;
  --patient-list-sixthWidth: 150px;
}

.patient-list {
  width: var(--patient-list-width);
  height: 100vh;
  display: flex;
  position: relative;
  flex-direction: column;
  line-height: normal;
  margin: 0 auto;
}

/* ---------- patient-list-header ---------- */
.patient-list-header {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 50px 0;
}

.patient-list-header-title {
  font-size: 38px;
  font-weight: 700;
}
.patient-list-header-register-link {
  text-decoration: none;
}
.patient-list-header-register {
  display: flex;
  align-items: center;
  font-size: 32px;
  font-weight: 700;
  color: black;
}
.patient-list-header-register::before {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #C0DEBB;
  background-image: url("../images/Feather\ Icon.png");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 5px;
}
.patient-list-header-user {
  position: relative;

}
.patient-list-header-user-logout {
  width: 24px;
  height: 24px;
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../images/logout.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}


/* ---------- patient-list-content ---------- */
.patient-list-content {
  width: 100%;
}

.patient-list-content-title {
  display: flex;
  margin-bottom: 12px;
}
.patient-list-content-title-item {
  display: flex;
  justify-content: center;
  font-size: 22px;
  font-weight: 400;
  text-align: center;
}
.patient-list-content-title-item:first-child {
  width: var(--patient-list-firstWidth);
}
.patient-list-content-title-item:nth-child(2) {
  width: var(--patient-list-secondWidth);
}
.patient-list-content-title-item:nth-child(3) {
  width: var(--patient-list-thirdWidth);
}
.patient-list-content-title-item:nth-child(4) {
  width: var(--patient-list-fourthWidth);
}
.patient-list-content-title-item:nth-child(5) {
  width: var(--patient-list-fifthWidth);
}
.patient-list-content-title-item:nth-child(6) {
  width: var(--patient-list-sixthWidth);
}

.patient-list-content-ol {
  display: flex;
  border-top: 1px solid var(--patient-list-border-color);
  cursor: pointer;
}
.patient-list-content-ol:last-child {
  border-bottom: 1px solid #acacac;
}
  
.patient-list-content-ol-item {
  font-size: 18px;
  font-weight: 400;
  padding: 5px 0;
  text-align: center;
  border-right: 1px solid var(--patient-list-border-color);
}
.patient-list-content-ol-item:first-child {
  width: var(--patient-list-firstWidth);
  border-left: 1px solid var(--patient-list-border-color);
}
.patient-list-content-ol-item:nth-child(2) {
  width: var(--patient-list-secondWidth);
}
.patient-list-content-ol-item:nth-child(3) {
  width: var(--patient-list-thirdWidth);
}
.patient-list-content-ol-item:nth-child(4) {
  width: var(--patient-list-fourthWidth);
}
.patient-list-content-ol-item:nth-child(5) {
  width: var(--patient-list-fifthWidth);
}
.patient-list-content-ol-item:nth-child(6) {
  width: var(--patient-list-sixthWidth);
  display: flex;
  justify-content: center;
}
/* 활성화 된 채팅이 있는 경우 class추가 */
.active::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #F4B4B4;
  background-image: url("../images/live\ chat.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 80%;
}